import React, { Component }  from 'react'
import { Link } from 'react-router-dom'
import {
  CButton,
  CCard,
  CCardBody,
  CCardGroup,
  CCol,
  CContainer,
  CForm,
  CInput,
  CInputGroup,
  CInputGroupPrepend,
  CInputGroupText,
  CRow
} from '@coreui/react'
import { Button, Card, CardImg,CardBody, CardGroup, Col, Container, Form, Input, InputGroup, InputGroupAddon, InputGroupText, Row, CardHeader } from 'reactstrap';
import CIcon from '@coreui/icons-react'
import logo from '../../../assets/ot.png'
import $ from 'jquery'
import axios from 'axios'
import  { Redirect } from 'react-router-dom'
import ReactDOM from 'react-dom'
import conf from '../../../conf.js'
import ReservationTerrainVisiteur from '../../visiteur/ReservationTerrainVisiteur'

class Login extends Component {

  constructor(props) {
    super(props)
    this.state = {
       ok:''
    }
  
}

componentDidMount() {
  $("#btn-charge").css("display","none")
  $("#echecAuth").css("display","none")

    var acces=JSON.parse(localStorage.getItem('token_auth'))
    console.log("acces",acces)
    try{
      if(acces.role=="ADMIN"){

        if(localStorage.getItem('connectReserver')){
          this.setState({ok:<Redirect to='/reservation-terrain-visiteur'/>})
        }else{
          this.setState({ok:<Redirect to='/dashboard'/>})
        }
        
       
       
      }
      if(acces.role=="PROPRIETAIRE_TERRAIN"){
        if(localStorage.getItem('connectReserver')){
          this.setState({ok:<Redirect to='/reservation-terrain-visiteur'/>})
        }
        this.setState({ok:<Redirect to='/liste-des-reservations'/>})
      }
      if(acces.role=="VISITEUR"){
        if(localStorage.getItem('connectReserver')){
          this.setState({ok:<Redirect to='/reservation-terrain-visiteur'/>})
        }
          this.setState({ok:<Redirect to='/mes-reservations'/>})
       
      }

    

      
     
   
    }catch(error){
    
    }
     
   
}

 
  handleLogin=()=>{
    
    $("#btn-valide").css("display","none")
    $("#btn-charge").css("display","block")

    var data= {
      "username": $("#username").val(),
      "password": $("#password").val(),
  
    }

    console.log("data a transmettre",data)

    const requestOptions = {
      method: 'POST',
      headers: {'Accept': 'application/json' },
      body: data
  };

  axios.post(conf.url+"user/login",data)
 // .then((response) => response.json())
  .then((response) => {
     console.log("okkk====>",response.data)
     
     try{
      
      if(response.data.roles.libelle=="ADMIN" || response.data.roles.libelle=="PROPRIETAIRE_TERRAIN" || response.data.roles.libelle=="VISITEUR" ){
        localStorage.setItem('token_auth', JSON.stringify({role:response.data.roles.libelle,user:response.data,token:response.data.monToken,id:response.data.id,nom:response.data.username,prenom:response.data.prenom}));
        
        window.location.reload(false);
        
     
      
        // $("#btn-valide").css("display","block")
        // $("#btn-charge").css("display","none")
  
       }else{
        $("#btn-valide").css("display","block")
        $("#btn-charge").css("display","none")
        $("#echecAuth").css("display","block")
        $("#username").val("")
        $("#password").val("")
       }
     }catch(error){
      $("#username").val("")
      $("#password").val("")
      $("#btn-valide").css("display","block")
      $("#btn-charge").css("display","none")
      $("#echecAuth").css("display","block")
      this.setState({ok:<Redirect to='/login'/>})
     }
    
   }
   
  )
  .catch((error) => {
    $("#btn-valide").css("display","block")
    $("#btn-charge").css("display","none")
    $("#echecAuth").css("display","block")
    $("#username").val("")
    $("#password").val("")
})
    
  }
  render() {
  return (
    <div className="c-app c-default-layout flex-row align-items-center mt-4">
      <CContainer>
        <CRow className="justify-content-center">
        
          <Col md="4">
                <CardGroup>
  
                  <Card className="p-4 bg-white text-info">   {this.state.ok}
                    <CardImg variant="top" src={logo} />
                    {/* <img src={logo} alt="" width="250" height="100"/> */}
                   
                    {/* <hr></hr> */}
  
                    <CardBody>
                      <Form>
                      <Row>
                      <div style={{textAlign:"center"}}>
                        <div style={{textAlign:"center"}} id="echecAuth" class="alert alert-danger" role="alert">
                          Mot de passe ou Login Incorrect !
                        </div>
                     </div>   
                    </Row>
                        <Row>
                          <InputGroup className="mb-3">
                            <InputGroupAddon addonType="prepend">
                              <InputGroupText>
                                <CIcon name="cil-user" />
                              </InputGroupText>
                            </InputGroupAddon>
                            <Input type="text" id="username" placeholder="Identifiant" autoComplete="username" />
                          </InputGroup>
                          
                          <InputGroup className="mb-4">
                            <InputGroupAddon addonType="prepend">
                              <InputGroupText>
                                <CIcon name="cil-lock-locked" />
                              </InputGroupText>
                            </InputGroupAddon>
                            <Input type="password" id="password" placeholder="Mot de passe" autoComplete="current-password" />
                          </InputGroup>

                          <Button id="btn-valide" onClick={this.handleLogin} color="success" className="px-4 text-white validation btn-block">Connexion</Button>
                          <Button
                            className="btn btn-info btn-block waves-effect waves-light"
                            disabled
                            id="btn-charge"
                          >
                            <span className="spinner-border spinner-border-sm" role="status" aria-hidden="true"> </span>
                            {/* <span> Connexion...</span> */}
                          </Button>
                        </Row>
                        
  
                        <Row>
                            <Button color="link" href="/#/forgetpwd" className="px-0 btn-block">Mot de passe oublié ?</Button>
                        </Row>

                        <hr></hr>

                        <Row>
                            {/* <Link to="/register"> */}
                              <Button color="info" href="/#/register" className="px-4 text-white validation btn-block">S'inscrire</Button>
                              {/* <button class="btn btn-primary confirmation" type="button" disabled>
                                <span class="spinner-border spinner-border-sm " role="status" aria-hidden="true"></span> En cours...
                              </button> */}
                            {/* </Link> */}
                            {/* <Button color="link" className="px-0 text-right">Mot de passe oublié ?</Button> */}
                        </Row>

                        

                        <Row>
                          <label id="msg"></label>
                        </Row>

                      </Form>
                    </CardBody>
                  </Card>
                  
                </CardGroup>
              </Col>
        </CRow>
      </CContainer>
    </div>
  )
}
}

export default Login
